import { userService } from 'Controllers/services/users/users.services';
import { alertActions } from './alert.actions';
import { history } from '../helper/history.helper';
export const userActions = {
    login,
    logout,
    register,
    signWithGoogle,
    signWithFacebook,
    getById,
    delete: _delete,
};
function getById(fbToken) {
    fbToken = localStorage.getItem('FCMT')
    console.log(fbToken);
}
// ? == Google [ACTION] 
function signWithGoogle(googleObj) {
    return dispatch => {
        dispatch(request({ googleObj }));
        userService.socialLogin(googleObj).then(
            res => {
                // ** Response Status */
                const status = res?.data?.status
                // ** Response Message */
                const resMSG = res?.data?.message
                if (status === 0) {
                    dispatch(failure(resMSG))
                    dispatch(alertActions.error(resMSG))
                    setTimeout(() => {
                        dispatch(alertActions.clear())
                    }, 1500)
                } else if (status === 1) {
                    //** Response Token */
                    const _BToken = res.data.data.user.token
                    //** Response Username */
                    const userName = res.data.data.user.username
                    localStorage.setItem('userToken', JSON.stringify({ _BToken, userName }))
                    const accessToken = JSON.parse(localStorage.getItem('userToken'))
                    history.push("/")
                    setTimeout(() => {
                        window.location.reload()
                    }, 1000)
                    dispatch(alertActions.success(resMSG))
                    setTimeout(() => {
                        dispatch(alertActions.clear())
                    }, 500)
                    dispatch(success(accessToken))
                }
            }
        )
    }
    function request(user) { return { type: 'USERS_LOGIN_REQUEST', user } }
    function success(user) { return { type: 'USERS_LOGIN_SUCCESS', user } }
    function failure(error) { return { type: 'USERS_LOGIN_FAILURE', error } }
}
