// ? == LOGIN [ACTION] 
function login(email, password, from, token) {
    return dispatch => {
        dispatch(request({ email }));
        userService.login(email, password, token)
            .then(
                res => {
                    // ** Response Status */
                    const status = res.data.status
                    // ** Response Message */
                    const resMSG = res.data.message
                    if (status === 0) {
                        dispatch(failure(resMSG))
                        dispatch(alertActions.error(resMSG))
                        setTimeout(() => {
                            dispatch(alertActions.clear())
                        }, 1500)
                    } else if (status === 1) {
                        //** Response Token */
                        const _BToken = res.data.data.user.token
                        //** Response Username */
                        const userName = res.data.data.user.username
                        localStorage.setItem('userToken', JSON.stringify({ _BToken, userName }))
                        const accessToken = JSON.parse(localStorage.getItem('userToken'))
                        history.push(from)
                        setTimeout(() => {
                            window.location.reload()
                        }, 1000)
                        dispatch(alertActions.success(resMSG))
                        setTimeout(() => {
                            dispatch(alertActions.clear())
                        }, 500)
                        dispatch(success(accessToken))
                    }
                }
            )
            .catch(
                error => {
                    dispatch(failure(error.toString()));
                    dispatch(alertActions.error(error.toString()));
                }
            );
    };
    function request(user) { return { type: 'USERS_LOGIN_REQUEST', user } }
    function success(user) { return { type: 'USERS_LOGIN_SUCCESS', user } }
    function failure(error) { return { type: 'USERS_LOGIN_FAILURE', error } }
}
// ? == LOGOUT [ // ACTION] 
function logout() {
    userService.logout();
    return { type: 'USERS_LOGOUT' };
}

// ? == REGISTER [ACTION] 
function register(user, from) {
    return dispatch => {
        dispatch(request(user));
        //* GET TOKEN
        // TODO ACTION WITH SERVICES
        userService.register(user).then(res => {
            let Msgs = res?.data?.message
            if (res.data.status === 0) {
                dispatch(failure(Msgs))
                dispatch(alertActions.error(
                    Msgs['fullname']
                        ? Msgs?.fullname : Msgs['username']
                            ? Msgs?.username : Msgs['email']
                                ? Msgs?.email : Msgs['password']
                                    ? Msgs?.password : false
                ));
            }
            else if (res.data.status === 1) {
                //** Response Token */
                const BToken = res.data.data.user.token
                //** Response Username */
                const userName = res?.data?.data?.user?.username
                localStorage.setItem('userToken', JSON.stringify({ BToken, userName }))
                const userToken = JSON.parse(localStorage.getItem('userToken'))
                // dispatch(success());
                history.push(from);
                setTimeout(() => {
                    window.location.reload()
                }, 1000)
                dispatch(alertActions.success('Registration ' + res.data.message))
                setTimeout(() => {
                    dispatch(alertActions.clear())
                }, 500)
                dispatch(success(userToken))
            }
        }
        ).catch(err => {
            dispatch(failure(err.toString()));
            dispatch(alertActions.error(err));
        })
    };

    function request(user) { return { type: 'USERS_REGISTER_REQUEST', user } }
    function success(user) { return { type: 'USERS_REGISTER_SUCCESS', user } }
    function failure(error) { return { type: 'USERS_REGISTER_FAILURE', error } }
}

// ! == GET ALL [ACTION] 
function getAll() {
    return dispatch => {
        dispatch(request());

        userService.getAll()
            .then(
                // users => dispatch(success(users => console.log(users))),
                error => dispatch(failure(error.toString()))
            );
    };

    function request() { return { type: 'USERS_GETALL_REQUEST' } }
    function success(users) { return { type: 'USERS_GETALL_SUCCESS', users } }
    function failure(error) { return { type: 'USERS_GETALL_FAILURE', error } }
}
// ! == DELETE_REQUEST [ACTION]
//* prefixed function name with underscore because delete is a reserved word in javascript
function _delete(id) {
    return dispatch => {
        dispatch(request(id));
        userService.delete(id)
            .then(
                user => dispatch(success(id)),
                error => dispatch(failure(id, error.toString()))
            );
    };
    function request(id) { return { type: 'USERS_DELETE_REQUEST', id } }
    function success(id) { return { type: 'USERS_DELETE_SUCCESS', id } }
    function failure(id, error) { return { type: 'USERS_DELETE_FAILURE', id, error } }
}