import './LoginForm.scss'
import { useMediaQuery } from 'react-responsive';
import { useHistory, useLocation } from 'react-router-dom';
import React, { useState, useEffect } from 'react'
import { useDispatch, useSelector } from 'react-redux';
import { userActions } from 'Controllers/services/Auth/actions/user.actions';
import { TextField, IconButton, InputAdornment } from '@material-ui/core';
import Visibility from '@mui/icons-material/Visibility';
import VisibilityOff from '@mui/icons-material/VisibilityOff';
import FacebookLogin from 'react-facebook-login'
import { GoogleLogin } from 'react-google-login';
import Banner from "../../../layout/Components/popups/Banner/Banner.layout"


export default function LoginForm() {
    const isSmallScreen = useMediaQuery({ query: '(max-width: 370px)' })
    // ? Var Hooks
    const history = useHistory();
    const dispatch = useDispatch();
    const location = useLocation();

    // ? Var State
    const [showPassword, setShowPassword] = useState(false);
    const [submitted, setSubmitted] = useState(false);

    // ? Var Selector
    const user = useSelector(state => state.authentication.user);
    const loggingIn = useSelector(state => state.authentication.loggingIn);

    // ? [Block Of Banner Message]
    const alert = useSelector(state => state.alert);
    const [bannerMessage, setBannerMessage] = useState()
    const [bannerType, setBannerType] = useState()
    const [bannerClick, setBannerClick] = useState(false)
    useEffect(() => {
        switch (alert.type) {
            case "alert-success":
                setBannerType('success'); setBannerMessage(alert.message); setBannerClick(true);
                break;
            case "alert-danger":
                setBannerType('error'); setBannerMessage(alert.message); setBannerClick(true);
                break;
            default:
                break;
        }
    }, [alert])

    // ** Functions [Blocks]
    //TODO Google response
    const responseGoogle = response => {
        const googleObj = { type: response?.tokenObj?.idpId, type_id: response?.googleId, email: response?.profileObj.email, name: response?.profileObj.name, image: response?.profileObj.imageUrl }
        dispatch(userActions.signWithGoogle(googleObj))
    }

    //TODO FaceBook response
    const componentClicked = data => console.log('')
    const resFacebook = response => {
        const facebookObj = { type: response?.graphDomain, type_id: response?.userID, email: response?.email, name: response?.name, image: response?.picture?.data?.url }
        dispatch(userActions.signWithFacebook(facebookObj))
    }

    //TODO Input on Change Function
    function handleChange(e) {
        const { name, value } = e.target;
        setInputs(inputs => ({ ...inputs, [name]: value }));
    }
    // ? State After Inilize
    const [inputs, setInputs] = useState({ email: '', password: '' })
    const { email, password } = inputs

    //TODO Input on Submit Function
    function handleSubmit(e) {
        e.preventDefault();
        setSubmitted(true);
        if (email && password) {
            //* get return url from location state or default to home page
            const { from } = location.state || { from: { pathname: "/" } }
            let token = localStorage.getItem('FCMT')
            dispatch(userActions.login(email, password, from, token))
        }
        inputs.email = ''
        inputs.password = ''
    }
    //* reset login status
    useEffect(() => {
        if (user) { history.push('/') }
    }, [])

    const loginFormTemp = () => {
        return (
            <div>
                {/* Message Alram */}
                <Banner type={bannerType} message={bannerMessage} clickMe={bannerClick} clickStatus={status => setBannerClick(status)} />
                {/* // ? Start Form */}
                <form name="form" onSubmit={handleSubmit} autoComplete='off'>
                    <div className="">
                        <p className="fCT-Light-sD m-0">welcom back</p>
                        <p className="fCT-Regular-sE mb-4">login with</p>
                    </div>
                    {/* // ? -- E-mail */}
                    <TextField
                        label="Email" placeholder="Email" style={{ borderRadius: '12px' }} className={`cCT-white-bg mb-4`}
                        variant="outlined" size='small' name='email' type="email" value={email} onChange={handleChange} fullWidth required />

                    {/* //? Password Lable */}
                    <TextField variant="outlined" size='small' label='Password' className={`cCT-white-bg mb-4`} style={{ borderRadius: '12px' }}
                        placeholder="Password" type={showPassword ? "text" : "password"} name='password'
                        value={password} onChange={handleChange}
                        InputProps={{
                            endAdornment: (
                                <InputAdornment position="end"><IconButton onClick={() => setShowPassword((prevState) => !prevState)}>{showPassword ? <VisibilityOff /> : <Visibility />} </IconButton></InputAdornment>)
                        }}
                        fullWidth required />

                    {/*// ? Forget Password Link */}
                    <span className="uCT-click w-100 fCT-Regular-sC cCT-gray-text m-0 ">forget password?</span>
                    {/* //? Login Button */}
                    <div className="my-4 form-group">
                        <button className="w-100 text-capitalize border-0 cCT-Gradian-Linear-Main uCT-f-radius-sA fCT-Regular-sC cCT-smoke-text p-2">
                            {loggingIn && <span className="spinner-border spinner-border-sm mr-1"></span>}
                            login
                        </button>
                    </div>
                </form>
                {/* Text */}
                <p className="fCT-Light-sE">or, sign in with</p>
                {/* //? Sign With  */}
                <div className="d-flex justify-content-start align-items-center mt-4 w-100">
                    {/* Google Button  */}
                    <div id='google' className={`${isSmallScreen ? 'mr-4' : 'mr-4'}`}>
                        <GoogleLogin
                            clientId="387567458482-g3qj7kk17tf1l8eiqm8cbd7374krnv1v.apps.googleusercontent.com"
                            onSuccess={responseGoogle} onFailure={responseGoogle}
                            autoLoad={false} cookiePolicy={'single_host_origin'}
                            buttonText={`Google`} className="CT-google-login uCT-brightness uCT-img-contain"
                        />
                    </div>
                    {/* Facebook Button  */}
                    <FacebookLogin
                        appId="4106974406094587"
                        fields="name,email,picture"
                        onClick={componentClicked} callback={resFacebook}
                        icon={`iCT-facebookLogin iCT-sC uCT-img-contain ${isSmallScreen ? 'm-0' : 'mr-2'}`}
                        cssClass="CT-facebook-login border-0 d-flex cCT-dark-text fCT-Regular-sD uCT-click uCT-brightness cCT-white-bg uCT-f-radius-sA "
                        textButton={`${isSmallScreen ? '' : 'Facebook'}`} size="large"
                    />
                </div>
            </div>
        )
    }
    return loginFormTemp()
}