import authHeader from "../Auth/helper/authHeader.helper";
import { API } from "../../../environment/environment";

export const userService = {
    login,
    logout,
    socialLogin,
    register,
    getAll,
    getById,
    update,
    delete: _delete,
};
// ? == LOGIN [POST]
function socialLogin(socialObj) {
    const requestOptions = socialObj
    return API.post("api/auth/socialLogins", requestOptions)

}
// ? == LOGIN [POST]
function login(email, password, token) {
    let loginInfo = { email, password, token }
    const requestOptions = loginInfo
    return API.post("api/auth/login", requestOptions)

}
// ? == REGISTER [POST]
function register(user) {
    const requestOptions = user
    return API.post(`api/auth/register`, requestOptions)
}

// ? == LOGOUT [Remove Local Storage]
function logout() {
    localStorage.removeItem('userToken')
    // localStorage.removeItem('FCMT')
}
// ! == GET ALL [GET]
function getAll() {
    const requestOptions = {
        method: 'GET',
        headers: authHeader()
    };
    return fetch(`/users`, requestOptions).then(res => {
        if (!res.ok) {
            console.log(res);
            throw Error("No Users")
        }
    }).catch((err) => {
        console.log(err);
    })
}
// ! == GET By ID [GET]
function getById(token) {
    const requestOptions = {
        token: localStorage.getItem('userToken')
    };
    return API.post(`api/auth/getUserData`, requestOptions).then(res => console.log(res))
}
// ! == UPDATE [PUT]
function update(user) {
    const requestOptions = {
        method: 'PUT',
        headers: { ...authHeader(), 'Content-Type': 'application/json' },
        body: JSON.stringify(user)
    };

    return fetch(`/users/${user.id}`, requestOptions).then(handleResponse);
}
// ! == DELETE [DELETE]
//* prefixed function name with underscore because delete is a reserved word in javascript
function _delete(id) {
    const requestOptions = {
        method: 'DELETE',
        headers: authHeader()
    };
    return fetch(`/users/${id}`, requestOptions).then(handleResponse);
}
// ? == RESPONSE [Handler]
function handleResponse(response) {
    return response => {
        const data = response && JSON.parse(response);
        if (!response.ok) {
            if (response.status === 401) {
                console.log(response);
                logout();
                window.location.reload(true);
            }
            const error = (data && data.message) || response.statusText;
            return Promise.reject(error);
        }
        return data;
    }
}

