import React, { useState, useEffect } from 'react';
import Icon from '../../shared/Icon/Icon'


export default function TagPriceMap({APIs, onHover, clickHandler, singleProperty}){
      const [APIsBundles, setAPIsBundles] = useState({
        id: Number,
        price: Number,
        currency_title: String,
    });
    function kFormatter(num) {
        if (Math.abs(num) > 999 && Math.abs(num) < 999999) {
            return Math.sign(num)*((Math.abs(num)/1000).toFixed(1)) + 'k'
        }else if(Math.abs(num) > 999999){
             return Math.sign(num)*((Math.abs(num)/1000000).toFixed(1)) + 'M'
        }else{
            return Math.sign(num)*Math.abs(num)
        }
        // return Math.abs(num) > 999 ? Math.sign(num)*((Math.abs(num)/1000).toFixed(1)) + 'k' : Math.sign(num)*Math.abs(num)
    }
    // console.log('From Tag Componant');
    useEffect(() => {
        switch(APIs.APIsKey){
            case "MapView" :
                setAPIsBundles(prevVal =>({...prevVal, 
                    id : APIs?.data?.property?.id,
                    price : APIs?.data?.property?.price,
                    currency_title : APIs?.data?.property?.currency?.title,
                }))
                break;
            }
    }, []);
    console.log(singleProperty);

    const TagPriceMapTemp = () => {
        return (
              <div className={`d-flex uCT-click iCT-sF`} onClick={() => {clickHandler(APIs?.data?.property)}}>
                  { APIs?.data?.property?.id != singleProperty?.id ? 
                    <i className={`iCT-priceTageMoove iCT-sE~ uCT-img-contain d-flex justify-content-center align-items-center pb-3 ${onHover == APIsBundles.id ? 'CT-Holder-Property-Selection' : ''}`} style={{width: '56.66px' , height: '82.59px'}}>
                        <p className={`mb-0 fCT-bold-sA cCT-main-text x d-flex flex-column align-items-center  uCT-t-2-all  uCT-f-radius-sC `}>
                            {/* {APIsBundles.price.toString().slice(0, APIsBundles?.price?.toString().length <= 6 ? 3 : 1 )}{APIsBundles?.price?.toString().length <= 6 ? 'K' : "M"} */}
                            {kFormatter(APIsBundles.price)}
                            <span className={`fCT-Regular-sA~ ml-1 ${onHover == APIsBundles.id ? 'cCT-gray-text' : 'cCT-gray-text'}`} style={{fontSize: '8px'}}>{APIsBundles.currency_title}</span>
                        </p>
                    </i>
                    :
                    <div className={`CT-Holder-Property-Selection`} style={{zIndex: '1'}}>
                        <Icon iconName='iCT-MiniPinMoove' iconSize='iCT-sC uCT-img-100' spaces='mr-1 fCT-Regular-sC'/>
                    </div>
                  }

              </div> 
        )
    }
    return TagPriceMapTemp()
}