import '../home.sass'
import ButtonRightIcon from 'view/features/shared/buttons/Button-Right-Icon/ButtonRightIcon'
import Icon from '../../../shared/Icon/Icon'
import { MediaCT } from 'view/viewLogic/view-logic'
import { _POST_ContactForm } from 'Controllers/services/contactForm/contactForm.services'
import { useState, useEffect } from 'react'
export default function ContactUsFeatures() {




    const [send, setSend] = useState(false)
    //** Input On Change */
    function handleChange(e) {
        const { name, value } = e.target;
        setContactForm(contactForm => ({ ...contactForm, [name]: value }));
    }
    //? Var State
    const [contactForm, setContactForm] = useState({ email: '', fullName: '', phone: '', subject: '', message: '', })
    const { email, fullName, phone, subject, message } = contactForm

    //** Send Messages */
    function handleSubmit(e) {
        console.log(contactForm);
        e.preventDefault();
        setSend(true)
        _POST_ContactForm(contactForm).then(res => {
            console.log(res);
            setSend(false)
        }).catch(err => {
            console.log(err);
            setSend(false)
        })
        setContactForm({ email: '', fullName: '', phone: '', subject: '', message: '', })
    }

    const contactUsFeaturesTemp = () => {
        return (
            <div className="">
                {/* Title Text */}
                <div className="container p-0 h-100 pb-5 mb-5 d-flex flex-column justify-content-center uCT-f-radius-sC ">
                    {/* Text Out Card */}
                    <div className={`${MediaCT('md') ? 'col' : 'm-0 col-8 '}`}>
                        <p className={`${MediaCT('md') ? 'fCT-bold-G pt-3 mt-3' : 'mb-4 pb-2 fCT-bold-54 text-capitalize'}`}>
                            Get in touch
                        </p>
                        <p className={`${MediaCT('md') ? 'fCT-Regular-sB' : 'fCT-Regular-sE mb-4 pl-4'}`}>
                            Have an inquiry or some feed back for us?
                            Fill out the form below to contact our team...
                        </p>
                    </div>
                    {/* Holder Container Card */}
                    <div className="imCT-contactUs uCT-img-cover uCT-f-radius-sC uCT-scale-in-animation uCT-click ">
                        <form onSubmit={handleSubmit}>
                            <div className={`${MediaCT('md') ? 'flex-column CT-Gradian-Linear-Opacity-BlackW-md' : 'd-flex CT-Gradian-Linear-Opacity-BlackW'} align-items-center m-0  uCT-f-radius-sC`}>
                                {/* Text Inside Card */}
                                <div className=" px-4 pt-4 pb-2 h-100 uCT-f-radius-sC cCT-white-text d-flex flex-column h-100 justify-content-center">
                                    <p className={`${MediaCT('lg') ? 'fCT-bold-G pt-2' : 'mb-2 fCT-bold-54 '}`}>Get in touch</p>
                                    <span style={{ fontStyle: 'normal' }} className={`${MediaCT('lg') ? 'fCT-Regular-sB' : 'fCT-Regular-sE mb-4 pl-4'}`}>Have an inquiry or some feed back for us?
                                        Fill out the form below to contact our team...
                                    </span>
                                </div>
                                {/* Form Input Holder */}
                                <div className="p-4">
                                    <div className="container-fluid">
                                        <div className="row align-items-center ">
                                            {/* First Name */}
                                            <div className={`${MediaCT('lg') ? 'col-12 p-0' : 'col-6 p-0 pr-2'}`}>
                                                <label className=" fCT-Regular-sC cCT-white-text pl-2 text-capitalize">Full Name</label>
                                                <div className="mb-1 d-flex cCT-Gradian-Linear-supp uCT-opacity-5 uCT-f-radius-sA cCT-Gradian-Linear-supp uCT-opacity-95 uCT-f-radius-sA">
                                                    <div className='d-flex align-items-center px-3 '>
                                                        <i className="uCT-click iCT-form-name-gray iCT-sB uCT-img-cover"></i>
                                                    </div>
                                                    <input className="w-100 cCT-inherit-bg uCT-input-outline-none fCT-Regular-sA cCT-gray-text text-capitalize pl-2 py-3"
                                                        type="text" placeholder="First Name" name="fullName" value={fullName} onChange={handleChange} autoComplete="off" required />
                                                </div>
                                            </div>
                                            {/* Subject */}
                                            <div className={`${MediaCT('lg') ? 'col-12 p-0 pt-4' : 'col-6 p-0 pl-2'}`}>
                                                <label className=" fCT-Regular-sC cCT-white-text pl-2 text-capitalize">Subject</label>
                                                <div className="mb-1 d-flex cCT-Gradian-Linear-supp uCT-opacity-95 uCT-f-radius-sA ">
                                                    <div className='d-flex align-items-center px-3'>
                                                        <i className="uCT-click iCT-form-name-gray iCT-sB uCT-img-cover"></i>
                                                    </div>
                                                    <input className="w-100 cCT-inherit-bg uCT-input-outline-none fCT-Regular-sA cCT-gray-text text-capitalize pl-2 py-3"
                                                        type="text" placeholder="Subject" name="subject" value={subject} onChange={handleChange} autoComplete="off" required />
                                                </div>
                                            </div>
                                            {/* Email Address */}
                                            <div className={`${MediaCT('lg') ? 'col-12 p-0' : 'col-6 p-0 pr-2'}`}>
                                                <label className=" fCT-Regular-sC cCT-white-text pl-2 pt-4 text-capitalize">e-mail address</label>
                                                <div className="mb-1 d-flex cCT-Gradian-Linear-supp uCT-opacity-95 uCT-f-radius-sA shadow-sm ">
                                                    <div className='d-flex align-items-center px-3 '>
                                                        <i className="uCT-click iCT-e-mailb-gray iCT-sB uCT-img-cover"></i>
                                                    </div>
                                                    <input className="w-100 cCT-inherit-bg uCT-input-outline-none fCT-Regular-sA cCT-gray-text text-capitalize pl-2 py-3"
                                                        type="email" placeholder="E-mail Address" name="email" value={email} onChange={handleChange} autoComplete="off" required />
                                                </div>
                                            </div>
                                            {/* Phone Number */}
                                            <div className={`${MediaCT('lg') ? 'col-12 p-0' : 'col-6 p-0 pl-2'}`}>
                                                <label className=" fCT-Regular-sC cCT-white-text pl-2 pt-4 text-capitalize">Phone Number</label>
                                                <div className="mb-1 d-flex cCT-Gradian-Linear-supp uCT-opacity-95 uCT-f-radius-sA shadow-sm ">
                                                    <div className='d-flex align-items-center px-3 '>
                                                        <i className="uCT-click iCT-e-mailb-gray iCT-sB uCT-img-cover"></i>
                                                    </div>
                                                    <input className="w-100 cCT-inherit-bg uCT-input-outline-none fCT-Regular-sA cCT-gray-text text-capitalize pl-2 py-3"
                                                        type="tel" placeholder="Phone Number" name="phone" autoComplete="off" value={phone} onChange={handleChange} required />
                                                </div>
                                            </div>
                                            {/* Message */}
                                            <div className="col-12 p-0 ">
                                                <label className=" fCT-Regular-sC cCT-white-text pl-2 pt-4 text-capitalize">message</label>
                                                <textarea className="p-4 w-100 fCT-Regular-sA uCT-input-outline-none cCT-gray-text cCT-Gradian-Linear-supp uCT-opacity-95 uCT-f-radius-sA text-capitalize"
                                                    name="message" value={message} onChange={handleChange} rows="7" placeholder='write a message' required></textarea>
                                            </div>
                                            {/* Send Button */}
                                            <div className="row w-100 d-flex m-0 pt-4 pb-2 align-items-center justify-content-end" >
                                                <ButtonRightIcon innerText='Send' btnICON={!send ? 'iCT-send-message-white' : 'iCT-send-message-white spinner-border spinner-border-sm mr-1'} btnCOLOR='cCT-Gradian-Linear-Main' iconBgCOLOR='cCT-inherit-bg' position='right' />
                                            </div>
                                        </div>
                                    </div>
                                </div>
                            </div>
                        </form>
                    </div>
                </div>
            </div>
        )
    }
    return contactUsFeaturesTemp()
}