import { AgenciesReducers } from 'Controllers/services/agency/agency.services'
import { AgentsReducers } from 'Controllers/services/agent/agent.services'
import { PropertiesReducer } from 'Controllers/services/property/property.services'
import { AdvancedSearchReducer } from 'Controllers/services/filter/advancedSearch.services'
import { RoutesReducers } from 'Controllers/appRouter.router'
import { combineReducers } from 'redux'
import { footerReducer } from './footer/footer.services'
import { alert } from './Auth/reducers/alert.reducer'
import { authentication } from './Auth/reducers/authentication.reducer'
import { registration } from './Auth/reducers/registration.reducer'
import { users } from './Auth/reducers/users.reducer'
export const allReducers = combineReducers({
    AgenciesReducers,
    AgentsReducers,
    PropertiesReducer,
    RoutesReducers,
    AdvancedSearchReducer,
    footerReducer,
    alert,
    authentication,
    registration,
    users
})