import './property-view-tool.sass'
import { useMediaQuery } from 'react-responsive';
import { useState, useEffect } from 'react'
import SelectBox from 'view/features/shared/SelectBox/SelectBox.feature'
import { _GET_AllCountry, _GET_AllCategory, _GET_AllType, _GET_AllCityOfCountry, _GET_AllAmenities, _GET_Price_Area } from 'Controllers/services/cores/coreData.services'
import { useSelector } from 'react-redux'
export default function PropertyViewsTool({toggleToGridCard,toggleToListCard,holderName}) {
    let MediaCT1118 = useMediaQuery({ query: `(min-width: ${'1118'}px)` })
    const advancedFilter = useSelector(state => state?.AdvancedSearchReducer?.advancedFilter)
    // core data
    const [coreData, setCoreData] = useState({
        cities: [],
        categories: [],
        type: [],
        amenity: [],
        price: [],
        size: []
    })
    // Get CORES APIs ---
    useEffect(() => {
        // City
        _GET_AllCityOfCountry('en', 1).then(res => {
            setCoreData(prevState => ({ ...prevState, cities: res?.data?.data }))
        }).catch()
        // Category
        _GET_AllCategory('en').then(res => {
            setCoreData(prevState => ({ ...prevState, categories: res?.data?.data }))
        }).catch()
        // Type
        _GET_AllType('en').then(res => {
            setCoreData(prevState => ({ ...prevState, type: res?.data?.data }))
        }).catch()
        // Amenities
        _GET_AllAmenities('en').then(res => {
            setCoreData(prevState => ({ ...prevState, amenity: res?.data?.data }))
        }).catch()
        // Price
        _GET_Price_Area().then(res => {
            setCoreData(prevState => ({ ...prevState, price: res?.data?.data?.price }))
            setCoreData(prevState => ({ ...prevState, size: res?.data?.data?.size }))
        }).catch()
        console.log(advancedFilter)
    }, [])
    const propertyViewsToolTemp = () => {
        
        return (
            <div className='container-fluid p-0 d-flex justify-content-between align-items-center w-100'>
                {/* Filters */}
                <div className={`d-flex flex-wrap w-100`}>
                    {/* Location Select Input */}
                    <SelectBox filterRes={advancedFilter?.city_id} HolderArray={coreData.cities} values={['id', 'title']}  MinWidth={100} inputLabel='Location' SelectLabel='Location' OptionValue={'elem'} Option={'elem'}/>
                    {/* Price Select Input */}
                    <SelectBox filterRes={advancedFilter?.price_min} HolderArray={coreData.price}  MinWidth={100} inputLabel='Min. Price' SelectLabel='Min. Price' OptionValue={'elem'} Option={'elem'}/>
                    <SelectBox filterRes={advancedFilter?.price_max} HolderArray={coreData.price}  MinWidth={100} inputLabel='Max. Price' SelectLabel='Max. Price' OptionValue={'elem'} Option={'elem'}/>
                    {/* Type Select Input */}
                    <SelectBox filterRes={advancedFilter?.categories} HolderArray={coreData.categories} values={['id', 'title']}  MinWidth={100} inputLabel='Type' SelectLabel='Type' OptionValue={'elem'} Option={'elem'}/>
                    {/* Category Select Input */}
                    <SelectBox filterRes={advancedFilter?.type_id} HolderArray={coreData.type} values={['id', 'title']}  MinWidth={100} inputLabel='Category' SelectLabel='Category' OptionValue={'elem'} Option={'elem'}/>
                    
                    
                    
                    {/* <button className='shadow-sm btn d-flex uCT-click uCT-brightness CT-holder-love-icon uCT-f-radius  fCT-Regular-sB align-items-center justify-content-start cCT-opacity-main-bg-10 mr-4 px-4 py-2'>
                        <i className='iCT-sB uCT-img-contain iCT-locationMain'></i>
                        <p className='mb-0 d-flex align-items-center cCT-dark-text mt-1 ml-2'>Location</p> 
                    </button>
                    <button className='shadow-sm btn d-flex uCT-click uCT-brightness CT-holder-love-icon uCT-f-radius  fCT-Regular-sB align-items-center justify-content-start cCT-opacity-main-bg-10 mr-4 px-4 py-2'>
                        <i className='iCT-sB uCT-img-contain iCT-PriceFilterMoove'></i>
                        <p className='mb-0 d-flex align-items-center cCT-dark-text mt-1 ml-2'>Price</p> 
                    </button>
                    <button className='shadow-sm btn d-flex uCT-click uCT-brightness CT-holder-love-icon uCT-f-radius  fCT-Regular-sB align-items-center justify-content-start cCT-opacity-main-bg-10 mr-4 px-4 py-2'>
                        <i className='iCT-sB uCT-img-contain iCT-Categoryfiltermoove'></i>
                        <p className='mb-0 d-flex align-items-center cCT-dark-text mt-1 ml-2'>Category</p> 
                    </button>
                    <button className='shadow-sm btn d-flex uCT-click uCT-brightness CT-holder-love-icon uCT-f-radius  fCT-Regular-sB align-items-center justify-content-start cCT-opacity-main-bg-10 mr-4 px-4 py-2'>
                        <i className='iCT-sB uCT-img-contain iCT-Typefiltermoove'></i>
                        <p className='mb-0 d-flex align-items-center cCT-dark-text mt-1 ml-2'>Type</p> 
                    </button> */}
                </div>
                {/* GRID & LIST BTN */}
                <div className={`d-flex mb-4 `}> 
                    {/* Grid BTN Icon */}
                    { MediaCT1118 ?
                        <button onClick={()=> {toggleToGridCard()}} className={`shadow-sm btn d-flex uCT-click uCT-brightness CT-holder-love-icon uCT-f-radius align-items-center ${ holderName == 'grid' ? 'cCT-main-bg' : 'cCT-opacity-main-bg-10'} mr-4 px-4`}>
                            { holderName == 'grid' ?
                            <>
                                <i className='iCT-sC uCT-img-contain iCT-gridwhite'></i>
                                <p className='mb-0 d-flex align-items-center cCT-white-text mt-1 ml-2'>Grid</p> 
                            </>
                            : 
                            <i className='iCT-sC uCT-img-contain iCT-gridMoove'></i>

                            }
                        </button> : false
                    }
                    {/* List BTN Icon */}
                    { MediaCT1118 ?
                        <button onClick={()=> toggleToListCard()} className={`btn px-4 d-flex uCT-click uCT-brightness CT-holder-love-icon uCT-f-radius align-items-center justify-content-center ${ holderName == 'list' ? 'cCT-main-bg' : 'cCT-opacity-main-bg-10'} `}>
                            { holderName == 'list' ?
                            <>
                                <i className='iCT-sC uCT-img-contain iCT-listwhite'></i>
                                <p className='mb-0 d-flex align-items-center cCT-white-text mt-1 ml-2'>List</p> 
                            </>
                            : 
                            <i className='iCT-sC uCT-img-contain iCT-listMoove'></i>
                            
                            }
                        </button> : false
                    }   
                </div>
            </div>
        )
    }
    return propertyViewsToolTemp()
}
