import "./App.css";
import Navbar from "./view/layout/Components/NavBar/Navbar";
import Footer from "./view/layout/Components/Footer/Footer";
import Subscribe from "./view/layout/Components/Subscribe/Subscribe";
import MasterContainer from "./view/layout/Components/Master-Container/MasterContainer";
import MobileDownNavBarLayout from "./view/layout/Components/Mobile-Down-NavBar/MobileDownNavBar.layout";
// import AdvancedSearch from 'view/features/shared/AdvancedSearch/AdvancedSearch'
import $ from 'jquery';
import TopCenterPopup from "view/layout/Components/popups/TopCenterPopup/TopCenter.popup";
import { ToggleNumbers } from "view/viewLogic/view-logic";
import { BrowserRouter as Router } from "react-router-dom";
import AdvancedSearch from "view/features/AdvancedSearch/AdvancedSearchHolder.feature";
import React, { useState, useEffect } from "react";
import { useSelector, useDispatch } from 'react-redux';
import { history } from "Controllers/services/Auth/helper/history.helper";
import { alertActions } from "Controllers/services/Auth/actions/alert.actions";
// import { notification } from "Controllers/services/Auth/NotificationToken/NotificationToken.services";

function App() {
  const alert = useSelector(state => state.alert);
  const dispatch = useDispatch();
  useEffect(() => {
    // notification.pushNotification()

    history.listen((location, action) => {
      // clear alert on location change
      dispatch(alertActions.clear())
    });
  }, [])


  const componentsWithoutLayout = useSelector(state => state?.RoutesReducers?.componentsWithoutLayout)
  const navPosition = useSelector(state => state?.RoutesReducers?.navPosition)
  const [scrollingUpORDown, setScrollingUpORDown] = useState(0);
  const [togglePositionForNav, settogglePositionForNav] = useState('sticky');
  // Advanced Search Section
  const [toggleOpenCloseTopCenter, setToggleOpenCloseTopCenter] = useState(false)
  let prevScrollpos = window.pageYOffset;
  const TopPopup = () => {
    return (
      <section className={``}>
        {/* Advanced Search */}
        <AdvancedSearch advancedSearch={() => { setToggleOpenCloseTopCenter(!toggleOpenCloseTopCenter) }} />
      </section>
    );
  };
  window.onscroll = function () {
    let currentScrollPos = window.pageYOffset;
    if (prevScrollpos > currentScrollPos) {
      setScrollingUpORDown(0);
      $('#mobileNavBar').addClass('CT-navUP')
      $('#mobileNavBar').removeClass('CT-navDown')
    } else {
      $('#mobileNavBar').addClass('CT-navDown')
      $('#mobileNavBar').removeClass('CT-navUP')
    }
    prevScrollpos = currentScrollPos;
  };
  useEffect(() => {
    window.scrollTo({
      top: 0,
      behavior: 'smooth'
    })    
  }, []);


  return (
    <div className="App p-0">
      <Router history={history}>
        <div>
          {/* navBar */}
          <nav className={`CT-nav-bar shadow-sm w-100 ${!navPosition ? 'position-sticky' : 'position-fixed'}`} >
            <Navbar
              advancedSearch={(JustClose = '') => {
                JustClose ? setToggleOpenCloseTopCenter(false) : setToggleOpenCloseTopCenter(!toggleOpenCloseTopCenter)
              }}
            />
          </nav>
          {navPosition ? <div style={{ height: '80px' }}></div> : false}
          <nav id='mobileNavBar'

            className=" CT-navUP uCT-t-2-all CT-nav-bar-mobile w-100 d-block d-md-none shadow-sm"
          >
            {!componentsWithoutLayout ?
              <MobileDownNavBarLayout />
              : false
            }
          </nav>
          {/* AdvancedSearch */}
          <div className={``}>
            <TopCenterPopup
              advancedSearch={() => { setToggleOpenCloseTopCenter(!toggleOpenCloseTopCenter) }}
              toggleOC={toggleOpenCloseTopCenter}
              content={TopPopup()}
            />
          </div>
          {/* MasterContainer */}
          <main className={``}>
            <MasterContainer />
          </main>
          {/* Footer */}

          {!componentsWithoutLayout ? <footer className="CT-footer">
            <Subscribe />
            <Footer />
          </footer> : false}
        </div>
      </Router>
    </div>
  );
}

export default App;
