import * as React from 'react';
import InputLabel from '@mui/material/InputLabel';
import MenuItem from '@mui/material/MenuItem';
import FormControl from '@mui/material/FormControl';
import Select from '@mui/material/Select';
import './SelectBox.sass'

export default function SelectBox({inputLabel = '', Option = '', OptionValue = '', SelectValue = '', SelectLabel = '', MinWidth = 80, HolderArray = [], values=[], filterRes=''}) {
    const [searchRes, setSearchRes] = React.useState('');

    const handleChange = (event) => {
        setSearchRes(event.target.value);
    };
    // console.log(age);
    React.useEffect(()=>{
        setSearchRes(filterRes)
    },[])
    const SelectBoxTemp = () => {

        return (
            <div className='mr-4 mb-4' style={{ flexGrow: 1 }}>
                <FormControl className="w-100" sx={{ m: '0', minWidth: MinWidth, flexGrow: 1 }}>
                    <InputLabel id="demo-simple-select-autowidth-label " classes={`cCT-main-text pb-2 `}>{inputLabel}</InputLabel>
                    <Select
                        className={` cCT-orange-text`}
                    labelId="demo-simple-select-autowidth-label"
                    id="demo-simple-select-autowidth"
                    value={searchRes}
                    onChange={handleChange}
                    autoWidth
                    label={SelectLabel}
                    >
                        <MenuItem value="" className={`px-4 py-2  d-flex cCT-dark-text fCT-Regular-sB`}>
                            <p className='m-0'>None</p>
                        </MenuItem>
                        {HolderArray?.map((elem, index) => {
                            return <MenuItem key={index} className={`px-4 py-2 cCT-main-text~ d-flex cCT-dark-text `} value={values[0] ? elem[values[0]] : elem}>{values[1] ? elem[values[1]] : elem}</MenuItem>
                        })}
                    </Select>
                </FormControl>
            </div>
        )
    }
    return SelectBoxTemp()
}
