import Button from '@material-ui/core/Button';
import { SnackbarProvider, useSnackbar } from 'notistack';
import React, { useEffect, useRef, useState } from 'react';

function MyApp({ message, type, clickMe, clickStatus }) {

    const elementRef = useRef();

    useEffect(() => {
        clickMe && elementRef.current.click()
        clickStatus(false)
    }, [clickMe])
    const { enqueueSnackbar } = useSnackbar();
    const handleClickVariant = (variant) => () => {
        // variant could be success, error, warning, info, or default
        enqueueSnackbar(message, { variant });
    };
    return (
        <Button ref={elementRef} className="d-none" id='x' onClick={handleClickVariant(type)}></Button>
    );
}

export default React.memo(function Banner({ message, type, clickMe, clickStatus }) {
    const [state] = useState({
        vertical: 'bottom',
        horizontal: 'right',
    });
    const { vertical, horizontal } = state;
    return (
        <SnackbarProvider autoHideDuration={2000} maxSnack={5} anchorOrigin={{ vertical, horizontal }}>
            {
                clickMe && <MyApp message={message} type={type} clickMe={clickMe} clickStatus={clickStatus} />
            }
        </SnackbarProvider>
    );
})
