import './PropertySMListCard.sass'
import Icon from '../../shared/Icon/Icon'
import React, { useState, useEffect } from 'react';
import { Link } from "react-router-dom";

export default function PropertySMListCard({APIs = {}}) {

    const [APIsBundles, setAPIsBundles] = useState({
        id: Number,
        rating: Number,
        image: String,
        title: String,
        country_title: String,
        type_title : String,
        category_title : String,
        high_light_title : Number,
        type_title : String,
        currency_title : Number,
        price : Number
    });

    useEffect(() => {
        switch(APIs.APIsKey){
            case "MapView" :
                setAPIsBundles(prevVal =>({...prevVal, 
                    id : APIs?.data?.property?.id,
                    rating : APIs?.data?.property?.avg_review,
                    image : APIs?.data?.property?.user?.image,
                    image_property : APIs?.data?.property?.images,
                    title : APIs?.data?.property?.title,
                    country_title : APIs?.data?.property?.city?.country?.title,
                    type_title  : APIs?.data?.property?.type?.title ,
                    category_title : APIs?.data?.property?.category?.title,
                    high_light_title : APIs?.data?.property?.high_light?.title,
                    currency_title : APIs?.data?.property?.currency?.title,
                    price : APIs?.data?.property?.price
                }))
                break;
            }
    }, []);
    console.log(APIs);
    const PropertySMListCardTemp = () => {
        return (
            <Link to={`/property-details/${APIsBundles.id}`} style={{color: 'black'}} className='CT-mini-SM-list-card-map uCT-f-radius-sC shadow-lg  d-flex flex-column justify-content-start align-items-center pt-2 cCT-support2-bg position-absolute'
            style={{bottom: '13px', left:'-24px', zIndex: '3'}}>
                {/* Section 2nd */} {/* Images Holders */}
                <div class=" uCT-f-radius-sB overflow-hidden CT-Image-Holder d-flex justify-content-center shadow" style={{ minWidth: '164px', minHeight: '110px'}}>
                    <i className={`imCT-PropertyListCover uCT-img-cover`}  style={{ backgroundImage: `url(${APIsBundles?.image_property && APIsBundles?.image_property[0]})`, minWidth: '164px', minHeight: '110px'}}></i>
                </div>
                <div className={`flex-column justify-content-center align-items-center py-3 px-2`}>
                    <div className='d-flex justify-content-center mb-3~ '>
                        {Array(APIsBundles?.rating).fill().map((elem, i) => {
                            return <i key={i} className='iCT-goldStar CT-star uCT-img-cover mr-1 my-1'></i> 
                        })}
                        { typeof(APIsBundles?.rating) != 'function' ?  Array( 5 - APIsBundles?.rating ).fill().map((elem, i) => {
                            return <i key={i} className='iCT-rategray CT-star uCT-img-cover mr-1 my-1'></i>
                        }):false}
                    </div>

                    <p className='mb-0 fCT-Regular-sA text-center px-3 my-2 CT-Text-ellipsis-2Line' style={{color: 'black'}}>{APIsBundles.title}</p>
                    
                    <div className='d-flex  align-items-end  justify-content-center w-100 '>
                        <Icon iconName='iCT-pricemoove' spaces='cCT-main-text fCT-bold-sC mr-1' iconSize='iCT-sB mr-2' textPosition='right' innerText={APIsBundles.price}/>
                        <p className='mb-0 cCT-gray-text fCT-bold-sB'>{APIsBundles.currency_title}</p>
                    </div>
                </div>
                <div className={`d-flex bg-danger~ justify-content-start w-100 pl-4`} style={{zIndex: '2'}}>
                    <Icon iconName='iCT-PolygonMap' iconSize='iCT-sC uCT-img-100' spaces='mr-1 fCT-Regular-sC '/>
                </div>
            </Link>
        )
    }
    return PropertySMListCardTemp()
}